<template>
    <div class="master">
        <div class="top">
            <div class="top-inner">
                <div class="top-left"></div>
                <div class="top-right">
                    <a class="git-link" href="http://171.16.46.5/wangqiang/war-wolf-ui" target="_blank">
                        <div class="github iconfont icongithub"></div>
                    </a>
                </div>
            </div>

        </div>
        <div class="block">
            <div class="left">菜单</div>
            <div class="main">主体</div>
        </div>
    </div>
</template>

<script>
    export default {
        name: 'Master',
        data() {
            return {

            }
        }
    }
</script>
<style scoped>
    .master{position: absolute;top:0px;left:0px;bottom:0px;right:0px;background: #f7f8fa;}
    .top{height: 60px;background: #001938;}
    .block{width: 1400px;overflow: hidden;position: absolute;bottom:0px;top:60px;left:50%;transform: translate(-50%)}
    .left{width: 220px;float:left;height: 100%;background: #fff;}
    .main{width: auto;margin-left:240px;height: 100%;overflow: hidden;}
    .top-left{height: 60px;width: auto;float:left;overflow: hidden;}
    .top-right{height: 60px;width: auto;float:right;overflow: hidden;}
    .top-inner{width: 1400px;height: 60px;margin:0 auto;}
    .git-link{height: 30px;width: 30px;display: block;}
    .github{color:#ccc;font-size: 26px;height: 30px;width: 30px;line-height: 30px;text-align: center;margin-top:15px;}
    .github:hover{color: #e9e8e2;cursor:pointer;}
</style>



